import React, { Component } from 'react';
import { Carousel } from "react-responsive-carousel";
import './App.css';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import pic1 from "./pic1.jpg";
import pic2 from "./pic2.jpg";
import pic3 from "./pic3.jpg";
import pic4 from "./pic4.jpg";


export default class App extends Component {
    render() {
        return (
            
            <div style={{ width: "90%",  display: "flex", justifyContent: "center",  overflow: "hidden" }}>
                <Carousel showThumbs={false} width="1700px">
                <div >
                        <div >
                            <img alt="" src={pic4} style={{ width: "1700px", height: "935px", objectFit:"cover" }} />
                            </div>
                    <p className="legend">This is my wife and I </p>
                </div>
                <div >
                        <img alt="" src={pic2} style={{ width: "1700px", height: "935px", objectFit: "cover"}}/>
                    <p className="legend">We love the Mountains</p>
                </div>
                <div>
                        <img alt="" src={pic3} style={{ width: "1700px", height: "935px", objectFit: "cover" }} />
                    <p className="legend">We love water</p>
                </div>
                <div>
                        <img alt="" src={pic1} style={{ width: "1700px", height: "935px", objectFit: "cover",  }} />
                    <p className="legend">Subscribe for more photos!!!</p>
                </div>


                </Carousel>
                </div>
        );
    }
}

